@import '../abstracts/variables';
@import '../abstracts/mixins';

footer{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    bottom: 0;
    width: 100%;
    background: linear-gradient(70deg,
    $primary-color,
    $secondary-color  75%
    );

    color: $text-color-white;

   .footer_container{
    display: flex;
    justify-content: space-around;
        .box{
            width: 400px;
            padding: 10px 20px;
        }

   }

   .left{
        .content{
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            .social{
                margin-top: 20px;
                svg{
                    transition: .2s linear;
                    margin-right: 7px;                     
                    &:hover{
                        fill: #fff;
                        transform: scale(1.15);
                    }
                }

            }
        }
   }

   .center{
        .content{
            .place{
                display: flex;
                align-items: center;
                margin: 10px 0;
                p{
                    margin-left: 20px;
                }
                svg{
                    --size: 32px;
                    height: var(--size);
                    width: var(--size);
                }
            }
            .phone{
                display: flex;
                align-items: center;
                margin: 22px 0;
                p{
                    margin-left: 20px;
                }
                svg{
                    --size: 32px;
                    height: var(--size);
                    width: var(--size);
                }
            }
            .email{
                display: flex;
                align-items: center;
                margin: 10px 0;
                p{
                    margin-left: 20px;
                }
                svg{
                    --size: 32px;
                    height: var(--size);
                    width: var(--size);
                }
            }
        }
   }
   .right{
        .content{
            .email{
                margin-top: 50px;
                input {
                    text-align: center;
                    min-width: 230px;
                    border-radius: 8px;
                    padding: .8rem;
                    outline: 0;
                    border: none;
                    background-position: 12px;
                    background-size: 15px;
                    font-size: 0.9em;
                    &::placeholder{
                        color: #4d4d4d;
                        font-weight: 500;
                    }
                }
    
                button{
                    @include btn-secondary;
                }
            }
    
        }
    }
    
}

